import React, { useMemo } from "react"

import styles from './UserStatistics.module.scss'

export type UserStatisticsProps = {
    className?: string
    likes: number
    views: number
    xs: number
    type: 'viewer' | 'performer'
    isLoading: boolean
    isVisible: boolean
}

export const UserStatistics = React.memo<UserStatisticsProps>(({
    className,
    likes,
    views,
    xs,
    type,
    isLoading,
    isVisible,
}) => {
    const classes = useMemo(() => [
        styles.root,
        isLoading ? styles['is-loading'] : '',
        !isVisible ? styles['is-hidden'] : '',
        className ? className : ''
    ].join(' '), [isLoading, isVisible, className])

    return (
        <div 
            className={classes}
        >
            <p className={styles.text}>
                {likes} Likes • {views} Views
            </p>
            {type === 'performer'
            ? (
                <p className={styles.text}> • {xs} XS</p>
            )
            : (
                <span className={styles.badge}>
                    17 XS
                    <IconQuestion />
                </span>
            )}
        </div>
    )
})

